/* eslint-disable no-debugger */
import React, { useState, useEffect, useContext } from "react"
import { Row, Col, BackTop, Form, Typography, Icon } from "antd"
import styled from "styled-components"
import { Heading, Layout, Section, Button, GifPlayer, Empty } from "components"
import { withRouter } from "react-router"
import { Formik } from "formik"
import TextLoop from "react-text-loop"
import ReactPlayer from "react-player"
import Carousel from "@brainhubeu/react-carousel"
import { useDispatch, useSelector } from "react-redux"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { withApollo, useQuery } from "react-apollo"

import { baseStyles } from "styles/base"
import { mobile, media, newClient, bumperAtHome, booksAtHome } from "helpers"
import orangeEl from "assets/images/ellipse-orange.png"
import purpleEl from "assets/images/ellipse-purple.png"
import homepageHero from "assets/images/homepage.svg"
import TextInput from "components/forms/TextInput"
import { QUERY_INTRO_VIDEO_HOME } from "queries/profile"
import { queryFetchSubjects } from "queries/data"
import { country, SocketContext } from "App"
import { countryMap } from "helpers/countryMapping"
import TutorCard from "./TutorCard"
import IntroBlock from "./IntroBlock"
import { fetchConsultants } from "store/actions/contentfulActions"
import { useTutors } from "helpers/hooks/data"
import DynamicIcon from "components/common/DynamicIcon"
import { GENDER, SERVER_BASE_URL } from "helpers/constants"
import HorizontalRow from "components/HorizontalRow"
import useHotKey from "helpers/hooks/useHotKey"

const programmeClient = newClient("programme")
const profileClient = newClient()

const { Paragraph } = Typography

const sequence = ["ArrowUp", "ArrowUp", "ArrowDown", "ArrowDown", "x", "x", "x", "Enter"]

function Home({ history, i18n, language }) {
	const { socket } = useContext(SocketContext)
	const dispatch = useDispatch()
	const currentCountry = countryMap[country]
	const consultants = useSelector(({ contentful }) => contentful.consultants)

	useHotKey(sequence, () => console.log("ANDHAN KIMAAAAAAAKKKK!!!"))

	useQuery(queryFetchSubjects, {
		client: programmeClient,
		variables: { country },
		onCompleted: (data) => {
			data = data.getProgrammeSubjectList || []
			const options = data.map(({ name, pvid }) => ({ value: Number(pvid), label: name }))
			setSubjectOptions(options)
		}
	})

	useQuery(QUERY_INTRO_VIDEO_HOME, {
		client: profileClient,
		variables: { country, limit: 16 },
		onCompleted: (data) => {
			data = data.introVideoHome || []
			setIntroVideoHome(data)
		}
	})

	const [selectedSubject, setSelectedSubject] = useState({ value: -1, label: "" })
	const [keywords, setKeywords] = useState("")
	const [introVideoHome, setIntroVideoHome] = useState([])
	const [subjectOptions, setSubjectOptions] = useState([])
	const [selectedIntroVideo, setSelectedIntroVideo] = useState({})
	const [findTutors, { data: tutors }] = useTutors()

	const handleSearch = (values) => {
		setKeywords(values.keywords)
		history.push(`/${country}/browse/tutors?keywords=${values.keywords}`)
	}

	const handleSearchBySubject = (selected) => {
		setSelectedSubject({ value: selected.value, label: selected.label })
		history.push(`/${country}/browse/tutors?subjects=${selected.value}&subjectLabel=${selected.label}`)
	}

	const handleClickTutor = (tutor) => {
		window.open(`/${country}/${tutor.token}--${tutor.pvid}--${tutor.firstName}--${tutor.lastName}`)
	}

	// eslint-disable-next-line react-hooks/exhaustive-deps
	useEffect(() => {
		// socket.on("FEEDS", (item) => {
		// 	console.log({ FEEDS: item })
		// })

		findTutors()

		dispatch(fetchConsultants())
	}, [dispatch, findTutors, socket])

	return (
		<Layout breadcrumb={false} language={language}>
			<BackTop />

			<Row className="mb4em">
				<Col lg={24}>
					<HeroImage>
						<Col lg={10}>
							<Section>
								<BoldHeading
									content={
										<p>
											<Trans>
												We help students to prepare for examinations in{" "}
												<TextLoop interval={2000}>
													<span className="primary">Singapore</span>
													<span className="primary">Indonesia</span>
													<span className="primary">Malaysia</span>
													<span className="primary">India</span>
													<span className="primary">Vietnam</span>
													<span className="primary">Philippines</span>
													<span className="primary">Sri Lanka</span>
												</TextLoop>
											</Trans>
										</p>
									}
									subheader={<Trans>It's easy to find your best tutors on Cudy. Try it today!</Trans>}
									color="white"
								/>
								<Formik onSubmit={handleSearch} initialValues={{ keywords: "" }}>
									{({ handleSubmit }) => (
										<Form layout="vertical" onSubmit={handleSubmit}>
											<TextInput
												name="keywords"
												placeholder={i18n._(t`Start by searching for subjects, e.g. English`)}
												width="100%"
												size="large"
											/>
										</Form>
									)}
								</Formik>
							</Section>
						</Col>
						<Col lg={10}></Col>
					</HeroImage>

					<IntroBlock />
				</Col>
			</Row>

			<Section className="pos-rel pt4em">
				<Donut src={orangeEl} alt="Orange donut" width="45%" bottom="-150px" right="-200px" />
				<Donut src={purpleEl} alt="Purple donut" left="-200px" />
				<MainRow gutter={!mobile && 32} marginBottom={mobile ? "1em" : "6em"}>
					<Col lg={12} xs={24} order={mobile && 2}>
						<ReactPlayer url={SERVER_BASE_URL + bumperAtHome} playsinline controls muted playing width="100%" />
					</Col>
					<Col lg={12} order={mobile && 1}>
						<BoldHeadPar
							content={
								<Paragraph>
									<Trans>
										Find <span className="primary">best tutors</span>
									</Trans>
								</Paragraph>
							}
							subheader={<Trans>See the tutors that match your criteria, and make an assignment</Trans>}
						/>
					</Col>
				</MainRow>

				<MainRow /* pl="0" pr="0" */ marginBottom="3em">
					<Col lg={24} xs={24}>
						<Heading
							content={
								<Trans>
									Featured tutors from <span className="primary">{currentCountry.name}</span>
								</Trans>
							}
						/>
						{tutors.length === 0 && <Empty description={<Trans>No featured tutors yet</Trans>} />}
						{tutors.length > 0 && mobile ? (
							<HorizontalRow height="300px" snapping>
								{tutors.map((person) => (
									<Col xs={10} key={person.pvid}>
										<TutorCard data={person} height="160px" onClick={handleClickTutor} />
									</Col>
								))}
							</HorizontalRow>
						) : (
							<TutorCarousel>
								{tutors.map((person) => (
									<TutorCard key={person.pvid} data={person} onClick={handleClickTutor} />
								))}
							</TutorCarousel>
						)}
					</Col>
				</MainRow>

				<MainRow marginBottom="5em">
					<Col lg={24} xs={24}>
						<Heading
							content={
								<Trans>
									Get to know our tutors from <span className="primary">{currentCountry.name}</span>
								</Trans>
							}
						/>
						{introVideoHome.length === 0 && <Empty description={<Trans>No introduction videos yet</Trans>} />}
						{introVideoHome.length > 0 && (
							<HorizontalRow height="260px">
								{introVideoHome.map((item) => {
									const url = SERVER_BASE_URL + item.videoIntroductionList.path
									return (
										<Col lg={5} xs={10} key={item.userPvid}>
											{/* <VideoCard path={item.videoIntroductionList.path} isUnauthorized /> */}
											<ReactPlayer
												preload="none"
												url={url}
												controls
												width="100%"
												height={mobile ? 160 : "100%"}
												onPlay={() => setSelectedIntroVideo(item)}
											/>
										</Col>
									)
								})}
							</HorizontalRow>
						)}
					</Col>
				</MainRow>

				<MainRow gutter={48} dataLength={subjectOptions.length}>
					<Col lg={12} xs={24}>
						<BoldHeading content={<Trans>Find subjects that are suitable the most to you</Trans>} />
						<Section centered className="px0">
							<img src={SERVER_BASE_URL + booksAtHome} width="80%" />
						</Section>
					</Col>
					<Col lg={12} xs={24} className="overflowing-section">
						<Row gutter={32}>
							{subjectOptions.map((item) => (
								<SubjectColumn key={item.value}>
									<Button
										block
										size="large"
										key={item.value}
										icon={item.icon}
										onClick={() => handleSearchBySubject(item)}
									>
										{item.label}
									</Button>
								</SubjectColumn>
							))}
						</Row>
					</Col>
				</MainRow>

				{consultants.length > 0 && (
					<>
						<MainRow marginBottom={mobile ? "0" : "0"}>
							<Col lg={24} xs={24}>
								<Heading
									level={4}
									content={<Trans>Not sure where/how to begin?</Trans>}
									subheader={<Trans>We have expert consultants in your area ready to help you</Trans>}
								/>
							</Col>
						</MainRow>
						<MainRow justify="start">
							<Col lg={16} xs={24}>
								<Row gutter={16} type="flex">
									{consultants.map((person) => (
										<Col lg={8} xs={12} key={person.whatsapp} className="mb2em">
											<Row>
												<Col lg={6}>
													<DynamicIcon
														type={person.gender === GENDER.male ? "icon-male" : "icon-female"}
														size={30}
													/>
												</Col>
												<Col lg={18}>
													<Heading
														reverse
														content={<Trans>{person.name}</Trans>}
														subheader={
															<Typography>
																<a
																	href={`https://wa.me/${person.whatsapp}?text=${i18n._(
																		t`Hi ${person.name}! I'd like to ask you something regarding the Cudy Marketplace`
																	)}`}
																	target="_blank"
																	rel="noopener noreferrer"
																>
																	+{person.whatsapp}
																</a>
																{person.city ? (
																	<Paragraph>{`${person.city}, ${person.country}`}</Paragraph>
																) : (
																	<Paragraph>{person.country}</Paragraph>
																)}
															</Typography>
														}
													/>
												</Col>
											</Row>
										</Col>
									))}
								</Row>
							</Col>
							<Col lg={4} xs={24}>
								<GifPlayer src="https://assets3.lottiefiles.com/temp/lf20_zsIYUf.json" width={250} height={mobile && 200} />
							</Col>
						</MainRow>
					</>
				)}
			</Section>
		</Layout>
	)
}

// prettier-ignore
export default withI18n()(withRouter(withApollo(Home)))

