const HeroImage = styled(Row).attrs(() => ({
	type: "flex",
	justify: "center",
	align: "middle"
}))`
	height: 500px;
	margin-bottom: 4em;
	background: ${`url(${homepageHero}) no-repeat`};
	background-size: cover;
	h2.ant-typography {
		color: #fff;
		line-height: 1;
		font-weight: bold;
		margin-bottom: 0.8em;
	}
`

const SubjectColumn = styled(Col).attrs(() => ({
	lg: 12,
	xs: 12
}))`
	margin-bottom: 1.5em;
	.ant-btn {
		height: 60px;
		border: none;
		box-shadow: ${baseStyles.boxShadow.mild};
		> span {
			text-overflow: ellipsis;
			overflow-x: hidden;
			max-width: 150px;
		}
		&:hover {
			transform: translateY(-3px);
			box-shadow: ${baseStyles.boxShadow.hover};
		}
	}
	${media.mobile`
        .ant-btn {
            > span {
                max-width: 120px;
            }
        }
    `}
`

const TutorCarousel = styled(Carousel).attrs({
	addArrowClickHandler: true,
	slidesPerPage: 6,
	slidesPerScroll: 6,
	breakpoints: { 414: { slidesPerPage: 2, slidesPerScroll: 2 } },
	arrowLeft: <Icon type="left" />,
	arrowRight: <Icon type="right" />
})`
	.BrainhubCarousel__trackContainer {
		.BrainhubCarousel__track {
			padding: 2em 1em;
		}
	}

	${media.mobile`
		overflow: visible;
		.BrainhubCarousel__customArrows.BrainhubCarousel__custom-arrowRight {
			position: relative;
			right: -10px;
		}
		.BrainhubCarousel__customArrows.BrainhubCarousel__custom-arrowLeft {
			position: relative;
			left: -10px;
		}
	`}
`

const BoldHeading = styled(Heading)`
	&& {
		margin-bottom: 2em;
		h2 {
			font-weight: bold;
			line-height: 1.2;
		}
	}
`

const BoldHeadPar = styled(BoldHeading)`
	&& {
		h2 {
			p {
				margin-bottom: 0;
			}
			.primary {
				color: ${baseStyles.primaryColor};
			}
		}
		.ant-typography:not(h2) {
			font-size: 1.4em;
		}
	}
`

const MainRow = styled(Row).attrs(() => ({
	type: "flex",
	justify: "center",
	align: "middle"
}))`
	padding-left: ${({ pl }) => pl || "8rem"};
	padding-right: ${({ pr }) => pr || "8rem"};
	margin-bottom: ${({ marginBottom }) => marginBottom || "4em"};

	.overflowing-section {
		max-height: ${({ dataLength }) => dataLength > 16 && "450px"};
		overflow-y: ${({ dataLength }) => dataLength > 16 && "scroll"};
		-webkit-overflow-scrolling: touch;
		&::-webkit-scrollbar-thumb {
			border-radius: 8px;
			border: 2px solid ${baseStyles.lightGrey.two};
			background-color: rgba(0, 0, 0, 0.5);
		}
		&::-webkit-scrollbar {
			-webkit-appearance: none;
		}
		&::-webkit-scrollbar:vertical {
			width: 11px;
		}
		&::-webkit-scrollbar:horizontal {
			height: 11px;
		}
	}

	${media.mobile`
        padding-left: 1em;
        padding-right: 1em;
        > .ant-col:first-child {
            margin-bottom: 3em;
        }
    `}
`

const Donut = styled.img.attrs(({ src, alt, width }) => ({
	src,
	alt,
	width: width || "40%"
}))`
	position: absolute;
	top: ${({ top }) => top && top};
	bottom: ${({ bottom }) => bottom && bottom};
	left: ${({ left }) => left && left};
	right: ${({ right }) => right && right};
	${media.mobile`
        display: none;
    `}
`
